.container {
  text-align: center;
}

.title {
  margin: 3.125rem 0;
  color: #fff;
  /*font-size: 3.75rem;*/
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap;
}

.title span {
  color: #26ffcb;
  padding-left: 0.9375rem;
}

.reset {
  width: 15.625rem;
  aspect-ratio: 1 / 0.3;
  border: none;
  outline: none;
  border-radius: 3.125rem;
  background: #1f3540;
  font-size: 1.625rem;
  color: #26ffcb;
  margin: 1.5625rem 0;
}

.boxes {
  display: flex;
  /*height: 11.25rem;*/
  aspect-ratio: 1;
  /*width: 11.25rem;*/
  background: #1f3540;
  border: 0.25rem solid #0f1b21;
  border-radius: 0.75rem;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  margin: 0.25rem;
}

.board {
  /*height: 37.5rem;*/
  max-width: 35.25rem;
  width: 100vw;
  aspect-ratio: 1;
  display: grid;
  margin: auto;
  grid-template-columns: repeat(3, 1fr);
  /*grid-column-gap: 0.425rem;*/
}

.boxes img {
  /*margin: 3.125rem 3.125rem;*/
  height: 50%;
  width: 50%;
}

.title img {
  padding: 1.25rem 1.25rem;
  height: 3.125rem;
}

@media only screen and (width <= 600px) {
  .reset {
    width: 10.625rem;
    font-size: 1.225rem;
  }

  .title {
    margin: 2.125rem 0;
  }

  .boxes {
    margin: 0;
  }
}

